body {
  background-color: #5d5c61;
  color: #fff;
  font-family: 'Aktifo', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
}

#video-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
}

.navbar {
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-radius: 0 0 8px 8px; /* add border radius to bottom corners */
}

.navbar a {
  font-weight: bold;
}

.navbar-brand {
  font-size: 28px;
  font-weight: bold;
  color: #fff;
  margin: 0.1;
  padding: 0 16px;
}

.navbar-menu {
  display: flex;
  list-style: none;
  background-color: transparent;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  margin-left: 24px;
  cursor: pointer;
  padding: 0;
}

.navbar-links {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 40px;
  height: 100%;
}

.navbar-links a {
  color: white;
  margin-left: 45px;
  font-size: 16px;
  text-decoration: none;
}

.navbar-menu a:hover {
  background-color: #2ecc71; 
}

.algorithm-box {
  display: flex;
  justify-content: center;
  margin-top: 32px;
  border-radius: 10px;
  padding: 10px;
}

.algorithm-select {
  font-size: 18px;
  padding: 8px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
}

.input-box {
  width: 400px;
  color: white;
  height: 20px;
  margin-top: 32px;
  padding: 16px;
  font-size: 24px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 8px;
}

.button2 {
  margin-top: 32px;
  display: inline-block;
  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  z-index: 1;
  color: #090909;
  padding: 0.7em 1.9em;
  font-size: 18px;
  border-radius: 0.5em;
  background: #35b9c0;
  border: 1px solid #e8e8e8;
}

.button2:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5,
             inset -4px -4px 12px #ffffff;
}

.button2:before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scaleY(1) scaleX(1.25);
  top: 100%;
  width: 140%;
  height: 180%;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.button2:after {
  content: "";
  position: absolute;
  left: 55%;
  transform: translateX(-50%) scaleY(1) scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: #009087;
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.button2:hover {
  color: #f3f3f3;
  border: 1px solid #009087;
}

.button2:hover:before {
  top: -35%;
  background-color: #009087;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.button2:hover:after {
  top: -45%;
  background-color: #009087;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.output-box {
  width: 400px;
  height: 20px;
  margin-top: 32px;
  padding: 16px;
  font-size: 24px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 8px;
}
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
}

.container {
  flex: 1;
  overflow-y: auto;
}

.footer {
  background-color: transparent;
  color: #fff;
  padding: 20px;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

.loader {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  position: fixed;
  left: 250px;
}

.loader__circle {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin: 0 4px;
  animation: loader_901 1s ease-in-out infinite;
}

.loader__circle:nth-of-type(1) {
  background-color: #007aff;
  animation-delay: 0;
}

.loader__circle:nth-of-type(2) {
  background-color: #ff2d55;
  animation-delay: 0.25s;
}

.loader__circle:nth-of-type(3) {
  background-color: #34c759;
  animation-delay: 0.5s;
}

.loader__circle:nth-of-type(4) {
  background-color: #ff9500;
  animation-delay: 0.75s;
}

@keyframes loader_901 {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.2);
  }

  40% {
    transform: scale(1);
  }
}